<template>
    <!-- PLANTILLA PARA LOS DEMAS COMPONENTES -->
    <v-app id="inspire">

      <v-navigation-drawer clipped fixed v-model="drawer" app>
        <v-list dense>

          <v-list-tile avatar>
            <v-list-tile-avatar>
              <img src="data:image/png;base64," >
            </v-list-tile-avatar>

            <v-list-tile-content>
              <v-list-tile-title>{{$username}}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>

          <v-list-tile @click="desconectar">
            <v-list-tile-action>
              <v-icon>remove_circle</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>Cerrar sesión</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>

        </v-list>
      </v-navigation-drawer>

      <v-toolbar app fixed clipped-left dark color="light-blue darken-4">
        <v-toolbar-side-icon v-if="$route.path != '/'"  @click.stop="drawer = !drawer"></v-toolbar-side-icon>
        <v-toolbar-title>Egresados UCM</v-toolbar-title>
      </v-toolbar>


      <v-content>
        <v-container fluid fill-height class="white">
          <v-layout justify-center >
            <v-flex>
                <keep-alive>
              <!-- Aqui se mostrara el contenido de las rutas -->
              <router-view/>
                </keep-alive>

            </v-flex>
          </v-layout>
        </v-container>
      </v-content>


      <v-footer app fixed class="light-blue darken-4">
        <span class="white--text"> &nbsp; Universidad Católica del Maule &copy; </span>
      </v-footer>
    </v-app>
</template>

<script>
import Vue from 'vue'

export default {
  name: 'App',
  data: function() {
      return{
          drawer: null
      }
  },
  methods: {
      desconectar: function(){

          this.$router.replace('/')
      }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



